@import '../index.scss';
.footer {
  background-color: #333;
  padding: 10rem 0;
  font-size: 1.4rem;
  text-align: center;

  .logo_box {
    text-align: center;
    margin-bottom: 8rem;
  }

  .logo {
    width: 15rem;
    height: auto;
  }

  .navigation {
    border-top: 2px solid $color-grey-dark;
    padding-top: 2rem;
    display: inline-block;
  }

  .list {
    list-style: none;
  }

  .item {
    display: inline-block;
    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }

  .link {
    &:link,
    &:visited {
      color: $color-grey-light-1;
      background-color: #333;
      text-decoration: none;
      text-transform: uppercase;
      display: inline-block;
      transition: all 0.2s;
    }

    &:hover,
    &:active {
      color: $color-primary;
      box-shadow: 0 1rem 2rem rgba($color-black, 0.4);
      transform: rotate(5deg) scale(1.3);
    }
  }

  .copyright {
    border-top: 2px solid $color-grey-dark;
    padding-top: 2rem;
    display: inline-block;
  }
}
